body {
  margin: 0;
}

.star {
  display: flex;
}

.star__item {
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;

  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.star :nth-last-child(n + 2) {
  background-image: url(images/star-active.svg);
}
